.hostels-main {
    margin-left: 290px;
    margin-right: 20px;
    margin-top: 100px;
    margin-bottom: 10%;
    font-family: Arial, Helvetica, sans-serif;
}

.hostel-image img {
    width: 550px;
    height: 470px;
}
.hosetl-image-1{
    position: relative;
    top: 0;
    bottom: 0;
    background-color: black;
}
.image-header{
    position: absolute;
    top: 200px;
    left: 210px;
    color: aliceblue;
    transition: opacity 0.3s ease-in-out;
    display: none;
}
.hosetl-image-1:hover .image-header{
    display: block;
}
.img-1:hover{
   opacity: 0.6;
}
.about-hostel{
    display: flex;
    gap: 20px;
    margin-top: 20px;
    transition: all ease-out 0.3s;
}
.sub-header{
    font-size: var(--subheading);
}
.hostel-paragraph p{
 font-size: 1.3rem;
 line-height: 27px;
 margin: 10px 0px;
 color:#4c5157;
}
.hostel-paragraph  h2{
    margin-bottom: 8px;
    margin-top: 0px;
    color:#8b598f;
}

@media screen and (max-width:1024px) {
    .image-block{
        margin: 10px;
    }
   .hostel-image{
    width: 100%;
    margin: 10px;
   }
    .hostels-main{
       margin-top: 150px;
       margin-left: 0px;
       margin-right: 0px;
       margin-bottom: 20%;
       width: 100%;
    }
    .hostel-image img{
        width: 400px;
    }
   
  .about-hostel{
    flex-wrap: wrap;
    margin-left: 0px;
    margin-right: 0px;
  }
    
    
}