.wellness-section{
    margin-top: 110px;
    margin-left: 280px;
    color: #333;
    margin-bottom: 10%;
}
.wellness-centre{margin-right: 10px;}
.wellness-header p{
font-weight: 600;
font-size: 2rem;
}
.wellness-text p{
    font-size: 1.3rem;
    line-height: 27px;
}
.well-coun{
    justify-content: space-around;
    flex-wrap: wrap;
}
.wellness-sub-header {
    font-size: 1.5rem;
}
.wellness-link{
    font-size: 1.2rem;
}
.counselling:hover{
    border: 1px solid #f43f5e;
}
.wellness-centre img{
    width: 45vw;
    height: 50vh;
}
.dean{width: 400px;}
.ambulance-no{animation: shad 1.5s linear infinite;}
@keyframes shad {
    0% {
        opacity: 1;
        scale: 1.1;

    }

    50% {
        opacity: 0.2;
    }

    100% {
        opacity: 1;
    }
}
.wellness-center:hover{border: 1px solid #f43f5e;}
@media (max-width:1024px) {
    .wellness-section{
        margin-left: 10px;
        margin-right: 10px;
        margin-top: 150px;
        margin-bottom: 30%;
        
        width: 100%;
    }
    .dean{width: 560px; height: 320px; margin: auto;}
    
    .wellness-section p{
        font-size: 1.5rem;
        line-height: 30px;
    }
    .wellness-section-s1{
        flex-wrap: wrap;
    }
    .wellness-counselling-ce p{
        font-size: 1.5rem;
    }
    .wellness-centre {
        margin: auto;
        margin-bottom: 30px;
        width: 100%;
      
        
    }
    .counselling-centre{
        width: 100%;
        margin-left: 40px;
    }
    .wellness-centre img{
        width:400px;
        height: 400px;

        
    }
    
    .well-coun{justify-content: center;flex-wrap: wrap;}
    .wellness-counselling-ce{flex-wrap: wrap; gap: 100px;}
}
