.main-gymkhana {
    margin-left: 290px;
    margin-right: 20px;
    margin-top: 120px;
    margin-bottom: 10%;
}

.gymkhana-sub-1:hover {
    background-color: #c084fc;
    color: #f8fafc;

}

.tech-board-sub-header {
    font-size: 1.6rem;
    font-weight: 600;
    margin: 25px 0px 10px 0px;
    padding-left: 10px;
    border-left: 5px solid #c00404;
}

.list-items li {
    font-size: 23px;
    margin-top: 20px;

}

.sentense-gymkhana p {
    font-size: 1.3rem;
    color: #4c5157;
    line-height: 25px;

}

.sports-section-header {
    margin: 30px 0px 10px 0px;
}

img {
    width: 300px;
    height: 300px;
    object-fit: cover;
    object-position: center;
    background-color: rgb(150, 149, 149);
    transition: all ease-in-out 0.3s;
   
}

img:hover {
    box-shadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
    opacity: 0.9;
    cursor: pointer;
}

.gymkhana-photos {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 5px;
}
.list-items p{
    font-size: 1.4rem;
    color: #0284c7;
}
.list-items ul li a:hover{
    color: #ef4444;

}

.gymkhana-sub-1{
    width: 180px;
    height: 140px;
}

@media (max-width:1024px) {
    .main-gymkhana {
        margin-left: 0px;
        margin-right: 0px;
        margin-top: 150px;
        margin-bottom: 50%;
        width: 100%;
    }
    .tech-board-sub-header{margin-left: 20px;}
    .board-flex{margin-left: 20px;}
.list-items{
    flex-wrap: wrap;
    margin-left: 20px;
}

.image-block img{
    width: 100%;
    height: 400px;
}
    .gymkhana-sub-1 {
        width: 100vw;
    }

    .board-flex {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
        justify-content: center;
    }
    .gymkhana-sub{
        flex-wrap: wrap;
        
    }
}

.blink-shadow {
    animation: shad 1.5s linear infinite;

}

@keyframes shad {
    0% {
        opacity: 1;
        scale: 1.1;

    }

    50% {
        opacity: 0.2;
    }

    100% {
        opacity: 1;
    }
}

.blink-shadow:hover {
    scale: 1;
    rotate: 180deg;
}