.admin-office{
    margin-left: 290px;
    margin-right: 20px;
    margin-top: 120px;
    margin-bottom: 10%;
}
.about-office{
    margin: 30px;
}

.des{
    font-size: 1rem;
    font-weight: 600;
    
    color: #343a40;
}
.admin-header p{
    font-size: 1.5rem;
    font-weight: 600;
}
.admin-office-main{
    gap: 20px;
}
.officers{
    padding-left: 40px;
}
.name{
    font-size: 1.2rem;
    font-weight: 600;
}
.officer-header{
    font-size: 1.3rem;
    font-weight: 600;
}
.hostels-unit{
    font-size: 1.3rem;
    font-weight: 600;
}
.officers{
    border-left: 1px solid black;
}
.student-admin-office{
    padding: 30px 100px;
}
@media screen and (max-width:1024px){
    .admin-office{
        margin-left: 0px;
        margin-right: 0px;
        margin-top: 130px;
        margin-bottom: 50%;
        
    }
    .student-admin-office{
        padding: 10px 70px;
    }
   
   .des{
    font-size: 1rem;
   }
    .admin-header{
        font-size: 3rem;
        font-weight: 600;
    }
   .about-office{
    margin-top: 20px;
    margin-left: 5px;
    margin-right: 5px;
   }
    .admin-office h4{
    font-size: 18px;
    }
    .admin-office p{
        font-size: 19px;
    }
    .admin-office  h2{
        font-size: 12px;
    }
    .admin-office-main{
       margin: auto;
    }
    .officers{
        border: none;
    }
}
