* {
  font-family: Arial, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
.sw-division-office{
  margin-left: 350px;
  margin-top: 150px;
  margin-bottom: 10%;
}
.fs-small{
  font-size: 1.1rem;
  color: #333;
}
.sw-organo{
  position: absolute;
   right: 100px;
    padding: 10px; 
    border: 2px solid #86198f; 
    font-size: 1.3rem; 
    font-weight: 600;
    width: 280px;
}

.wrapper>div {
  border: 2px solid  #fae8ff;
  border-radius: 5px;
  background-color: #fae8ff;
  color: black;
}

.first-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px;
  width: 380px;
  height: 250px;

}
.first-info img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
}
.office-staff {
  display: flex;
  flex-direction: column;
  padding: 30px;
  gap: 1rem;
}
.office-staff img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

.office-staff-1 {
  display: flex;
  align-items: center;
  gap: 10px;
}

.office-staff-1 .info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  padding-left: 15px;
}
.click-here{animation: shad 1.5s linear infinite;}
@keyframes shad {
    0% {
        opacity: 1;
        scale: 1.1;

    }

    50% {
        opacity: 0.2;
    }

    100% {
        opacity: 1;
    }
}
.sw-organo{animation: shadd 1.5s linear infinite;}
@keyframes shadd{
  0%{
    
    border-bottom: 11px solid #86198f;
  }
  50%{
   
  }
  100%{
    
  }
}

@media screen and (max-width:1024px) {
  .sw-division-office{
    margin-left: 0px;
    
    margin-bottom: 50%;
   
  }
  .designation{
    font-size: 1.3rem;
  }
  .about-des{
    font-size: 1.2rem;
    color: #2E2E2E;
  }
  .name{
    font-weight: 600;
    font-size: 1.3rem;
  }
  .wrapper{
    flex-wrap: wrap;
   
    gap: 40px;
  }
  .first-info{
    width: 80vw;
    margin:auto;
    height: 310px;
  }
  .sw-division-office{
    margin-top: 150px;
    
  }
  .sw-division-office-1{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }
  .about-info{
    font-size: 1.2rem;
  }
.dean{
  width: 400px;
}

  
  .sw-organo{
    position: relative;
    top: 130px;
    left: 1px;
    right: 10px;
    width: 280px;
    height: 100px;
    display: flex;
    flex-direction: row;
    margin-left: 20px;
  }
  
}
